@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@200;300;400&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  width: 100vw;
  height: 100%;
  overflow-x: hidden;
  background-color: #efe5df;
  background-color: var(--secondaryColor);
  background-color: #f3f3f3;
}
@font-face {
  font-family: leagueSpartan;
  src: url(/portfolio/static/media/LeagueSpartan-Bold.caadcad8.otf);
}
@font-face {
  font-family: uncialantiqua;
  src: url(/portfolio/static/media/UncialAntiqua-9mR7.eea5255a.ttf);
}

:root {
  --primaryColor: #000;
  --secondaryColor: #efe5df;
  --menuColor: #4c4f65;
  --menuHover: #000;
  --aboutTextColor: rgb(34, 34, 34);
  --aboutSpanColor: #000;
}
.dark_theme {
  --primaryColor: #efe5df;
  --secondaryColor: #000;
  --menuColor: #b0b2c3;
  --menuHover: #fff;
  --aboutTextColor: #a0a0a0;
  --aboutSpanColor: #fff;
}

.footer_container {
  width: 100%;
  background-color: var(--secondaryColor);
  border-top: 1px solid #7f7f7f;
}
.footer {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-content: center;
  padding-top: 10px;
}
.footer li {
  list-style: none;
  font-size: 1.5vw;
  font-family: "Poppins";
  letter-spacing: 1px;
  padding-top: 10px;
  transition: all 0.6s;
}
.footer li:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
a {
  text-decoration: none;
  color: var(--primaryColor);
  text-transform: uppercase;
  cursor: none;
}
.footer_text {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.1vw;
  text-transform: capitalize;
  padding: 10px 0px 20px;
  color: var(--primaryColor);
  font-family: poppins, sans-serif;
}
@media screen and (max-width: 768px) {
  .footer li {
    font-size: 3vw;
  }
  .footer_text {
    font-size: 2vw;
  }
}

@media screen and (max-width: 480px) {
  .footer li {
    font-size: 4vw;
  }
  .footer_text {
    font-size: 3vw;
  }
}

ul.navbar {
  width: 100%;
  color: #efe5df;
  display: flex;
  justify-content: space-between;
  text-transform: uppercase;
  z-index: 100;
  mix-blend-mode: difference;
  pointer-events: none;
}
ul.navbar li {
  list-style: none;
  font-family: "Nunito Sans", sans-serif;
  font-size: 17px;
  padding: 15px;
  position: relative;
  transition: all 0.2s;
  letter-spacing: 1px;
  mix-blend-mode: difference;
  pointer-events: auto;
}

.about_container {
  width: 100vw;
  background-color: var(--secondaryColor);
  color: var(--primaryColor);
  position: relative;
}
.about_container::selection,
.about_background_text::selection {
  background-color: none;
}

.about_me_text {
  font-size: 4.7vw;
  line-height: 1.5;
  padding: 100px;
  font-family: "Poppins", sans-serif;
  text-shadow: 0.3vw 0.1vh rgb(2 183 182 / 30%),
    -0.3vw -0.3vh rgb(183 2 2 / 40%);
  letter-spacing: 3px;
  text-transform: uppercase;
  color: var(--aboutTextColor);
}
.about_me_text span {
  color: var(--aboutSpanColor);
}
.name p {
  font-size: 2.5vw;
  font-family: "Nunito Sans", sans-serif;
  opacity: 0;
  transition: all 0.6s 0.2s;
  -webkit-transform: translateY(40px);
          transform: translateY(40px);
}
.name.show_name p {
  opacity: 1;
  -webkit-transform: translateY(20px);
          transform: translateY(20px);
}

.about_me_icon_container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.about_me_icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 31px;
  z-index: 3;
}
.about_icon {
  width: 50px;
  height: 50px;
}
.icon_label {
  color: #1c1d25;
  font-size: 10px;
  font-family: "leagueSpartan";
  text-align: center;
  transition: all 0.6s;
  -webkit-transform: translateY(10px);
          transform: translateY(10px);
  opacity: 0;
}
.about_me_icon:hover .icon_label {
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
  opacity: 1;
}
.about_me_icon:hover .react_icon {
  color: #61dafb;
}
.about_me_icon:hover .css_icon {
  color: #e54d26;
}
.about_me_icon:hover .html_icon {
  color: #24d;
}
.about_me_icon:hover .javascript_icon {
  color: #ecd120;
}
.about_me_icon:hover .redux_icon {
  color: rgb(100, 34, 221);
}
@media screen and (max-width: 480px) {
  .about_background_text {
    top: 20%;
    left: 10%;
    letter-spacing: 3px;
  }
  .about_me_head {
    font-size: 5vw;
  }
}
@media screen and (max-width: 768px) {
  .about_me_text {
    padding: 100px 50px 50px;
  }
}

