.cursor {
  width: 10px;
  height: 10px;
  background-color: #efe5df;
  border-radius: 50%;
  mix-blend-mode: difference;
  position: absolute;
  z-index: 100;
  pointer-events: none;
}
@media screen and (max-width: 768px) {
  .cursor {
    display: none;
  }
}

.about_screen_container {
  cursor: none;
}
.about_container {
  position: absolute;
  top: 0;
  left: 0;
}

.button_container {
  position: absolute;
  top: 50%;
  left: 50px;
  cursor: pointer;
}
.button {
  height: 44px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 15px;
  font-family: leagueSpartan, serif;
  text-transform: capitalize;
  font-weight: 600;
  position: relative;
  letter-spacing: 2px;
  background-image: linear-gradient(to right, #f06449, #ef3636);
  background-size: 200% 100%;
  transition: all 1s;
  padding: 0px 15px;
}
.button_mask {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  overflow: hidden;
}
.button_mask::before {
  content: "";
  position: absolute;
  top: 0;
  left: -10px;
  width: 80%;
  height: 100%;
  background-image: linear-gradient(#213777, #235aa6);
  -webkit-transform: translateX(-110px);
          transform: translateX(-110px);
  transition: all 0.6s;
}
.button:hover .button_mask::before {
  -webkit-transform: translateX(200px);
          transform: translateX(200px);
  outline: none;
  border: none;
}
.button:hover {
  outline: none;
  border: none;
}
.button .btn_text {
  position: relative;
  z-index: 1;
}
.rightArrow {
  width: 35px;
  height: 35px;
  position: absolute;
  top: 3px;
  right: -27px;
  color: var(--primaryColor);
  transition: all 0.5s;
}
.button:hover .rightArrow {
  -webkit-transform: translateX(5px);
          transform: translateX(5px);
}

.whole_work_card_container {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  padding: 120px 50px 0px;
  overflow-x: hidden;
}
.work_card_container {
  position: relative;
  width: 80%;
  background-color: rgba(28, 29, 37, 0.9);
  background-image: linear-gradient(to right, rgba(2, 8, 26, 0.9), #1c1d25);
  transition: all 0.5s;
  margin: 0px auto 135px;
  box-shadow: 0px 20px 80px 0px rgb(0, 0, 0, 0.3);
}
.work_card_container::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, #101b3b, #235aa6);
  z-index: 1;
  transition: all 1s;
}
.work_card_container.reveal::before {
  width: 0;
}
.work_card_wrapper {
  width: 100%;
  padding-top: 56.25%;
  position: relative;
}

.card {
  position: relative;
  width: 100%;
  height: 100%;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  opacity: 0.5;
}
.card_wrapper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.card::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0%;
  height: 100%;
  background-image: linear-gradient(
    to right,
    rgba(2, 13, 46, 0.9),
    rgba(35, 90, 166, 0.9)
  );
  transition: all 0.5s;
}
.work_card_container:hover .card::before {
  width: 100%;
}

.card_text_section {
  width: 85%;
  position: absolute;
  bottom: 90px;
  font-family: leagueSpartan;
  color: #fff;
  transition: all 0.3s linear;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.card_text_section .title {
  font-size: 4vw;
  font-family: poppins, sans-serif;
  text-transform: capitalize;
}
.card_text_section .subtitle {
  font-size: 1.4vw;
  font-family: poppins, serif;
  font-weight: 400;
  font-style: italic;
  margin-bottom: 20px;
  margin-top: 5px;
  color: #fff;
}
.card_text_section .button .rightArrow {
  color: #fff;
}

.card_number {
  position: absolute;
  top: -33px;
  font-family: "leagueSpartan", sans-serif;
  font-size: 13vw;
  opacity: 0;
  transition: all 0.4s linear 0.2s;
  -webkit-transform: translateY(10%);
          transform: translateY(10%);
  color: #fff;
  color: transparent;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  -ms-background-clip: text;
  -o-background-clip: text;
  background-image: linear-gradient(rgb(182, 179, 179) 50%, #fff 50%);
  background-size: 100% 200%;
  background-position: top left;
}
.work_card_container:hover .card_number {
  background-position: bottom left;
}
.work_card_container:hover .card_number {
  opacity: 1;
  -webkit-transform: translateY(0%);
          transform: translateY(0%);
}
.work_card_container:hover .card_text_section {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.work_card_container:nth-child(even) {
  -webkit-transform: translateX(-30px);
          transform: translateX(-30px);
}
.work_card_container:nth-child(odd) {
  -webkit-transform: translateX(30px);
          transform: translateX(30px);
}
.work_card_container:nth-child(even) .card_text_section {
  width: 82%;
  right: 10%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
.work_card_container:nth-child(odd) .card_text_section {
  left: 10%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.work_card_container:nth-child(even) .card_number {
  left: -50px;
}
.work_card_container:nth-child(odd) .card_number {
  right: -30px;
}
/* ------for tablet------------ */
@media screen and (max-width: 768px) {
  .whole_work_card_container {
    padding: 120px 0px;
  }
  .work_card_container {
    width: 90%;
    margin-bottom: 100px;
  }
  .card_text_section {
    bottom: 50px;
  }

  .work_card_container:nth-child(even),
  .work_card_container:nth-child(odd) {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  .card_text_section .subtitle {
    font-size: 2vw;
  }
  .work_card_container:nth-child(even) .card_number {
    left: -4%;
    top: -12%;
  }
  .work_card_container:nth-child(odd) .card_number {
    right: 0%;
    top: -12%;
  }

  .card_number {
    visibility: hidden;
  }
  .card_number.reveal_card_number {
    visibility: visible;
    opacity: 1;
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    background-position: bottom left;
  }
}
/* --------for mobile---------- */
@media screen and (max-width: 480px) {
  .whole_work_card_container {
    padding: 80px 0px;
  }
  .work_card_container:nth-child(even),
  .work_card_container:nth-child(odd) {
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
  }
  .card_text_section {
    bottom: 25px;
  }

  .card_text_section .title {
    font-size: 5vw;
  }
  .card_text_section .subtitle {
    font-size: 3.3vw;
    margin-bottom: 10px;
  }
}

.scroll_container {
  display: flex;
  align-items: center;
  /* animation: scrollBollMove 2s linear infinite; */
}
.scroll {
  width: 20px;
  height: 34px;
  border: 2px solid var(--primaryColor);
  border-radius: 22px;
  display: flex;
  justify-content: center;
  cursor: pointer;
}
.scroll_boll {
  width: 3px;
  height: 8px;
  background-color: var(--primaryColor);
  border-radius: 5px;
  -webkit-animation: scrollBollMove 2s linear infinite;
          animation: scrollBollMove 2s linear infinite;
}
@-webkit-keyframes scrollBollMove {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
}
@keyframes scrollBollMove {
  0% {
    -webkit-transform: translateY(0px);
            transform: translateY(0px);
  }
  100% {
    -webkit-transform: translateY(15px);
            transform: translateY(15px);
  }
}
.down-arrow {
  color: var(--primaryColor);
  width: 20px;
  height: 20px;
  -webkit-animation: down-arrow 2s 2s linear infinite;
          animation: down-arrow 2s 2s linear infinite;
}
@-webkit-keyframes down-arrow {
  0% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
  100% {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
  }
}
@keyframes down-arrow {
  0% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
  100% {
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
  }
}

.home_container {
  width: 100vw;
  height: 100vh;
  background-color: var(--secondaryColor);
  position: relative;
  overflow: hidden;
  cursor: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
ul.navbar {
  position: fixed;
  top: 0;
}
ul.home_nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
  color: var(--primaryColor);
  text-transform: uppercase;
  position: absolute;
  bottom: 10px;
}
.home_nav li {
  list-style: none;
  font-family: "Nunito Sans", sans-serif;
  font-size: 17px;
  padding: 15px;
  position: relative;
  transition: all 0.2s;
  font-weight: 400;
}

.home_content {
  font-size: 13vw;
  color: var(--primaryColor);
  padding: 80px 10px 50px;
  transition: -webkit-transform 0.6s;
  transition: transform 0.6s;
  transition: transform 0.6s, -webkit-transform 0.6s;
  font-family: uncialantiqua;
}
.line_second,
.line_first {
  display: inline-block;
}
@media screen and (max-width: 480px) {
  .home_container {
    width: 100vw;
    height: 90vh;
  }

  ul.navbar {
    position: absolute;
  }
}
@media screen and (min-width: 992px) {
  .home_content {
    font-size: 13.5vw;
    padding: 0px 10px;
  }
}

.whole_work_card_container {
  background-color: #efe5df;
}
.homescreen_container {
  cursor: none;
}

